.row{
    flex-direction: row;
}
.column{
    flex-direction: column;
}
.row, .column{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.row-between, .column-between{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}
.column-between{
    flex-direction: column;
    justify-content: space-between;
}
.row-between{
    flex-direction: row;
    justify-content: space-between;
}
.row-left, .column-top{
    justify-content: flex-start;
}
.row-right, .column-bottom{
    justify-content: flex-end;
}
.column-left, .row-top{
    align-items: flex-start;
}
.column-right, .row-bottom{
    align-items: flex-end;
}
.mossane-btn{
    outline: none;
    border: none;
    background-color: var(--secondary-color);
    padding: 10px 20px;
    padding-right: 30px;
    color: var(--white-color);
    font-weight: bold;
    cursor: pointer;
    border: 2px solid var(--secondary-color);
    transition: 0.5s ease-in-out;
}
.mossane-btn:hover{
    background-color: #18070094;
    color: var(--secondary-color);
}
.mossane-btn-anim{
    position: absolute;
    transition: 0.4s ease-in-out;
    top: 50%;
    right: 15px;
    -webkit-transform: translate(0px, -50%);
            transform: translate(0px, -50%);
    
}
.mossane-btn:hover>.mossane-btn-anim{
    -webkit-transform: translate(5px, -50%);
            transform: translate(5px, -50%);
}
.mossane-header {
  position: relative;
  width: 100%;
  height: 100vh;
  z-index: 500;
  background-image: url(/mossane-patrimoine/static/media/background.5c766456.webp);
  background-position: left;
  background-repeat: no-repeat;
  background-size: cover;
}
.mossane-header>hgroup, .mossane-header>p{
  -webkit-transform: translateY(-75px);
          transform: translateY(-75px);
}
.mossane-header>hgroup>h1, .mossane-header>hgroup>h2{
  font-family: 'Open Sans'!important;
}
.mossane-navbar {
  position: fixed;
  left: 0%;
  top: 0%;
  padding: 0% 10%;
  transition: 0.4s ease-in-out;
  z-index: 1000;
}
.mossane-navbar-reveal{
  background-color: var(--grey-color);
  box-shadow: 0px 0px 15px black;
  padding: 0% 2%;
}
.mossane-navbar-reveal>#logo{
  width: 40px;
}
.mossane-link {
  list-style: none;
  padding: 10px;
}
.mossane-link:hover > a {
  color: var(--secondary-color);
}
.mossane-link-active > a {
  color: var(--secondary-color);
}
#logo {
  width: 50px;
  transition: 0.3s ease-in-out;
}
.mossane-dropdown {
  position: relative;
}
.mossane-dropdown-hover {
  position: absolute;
  top: 100%;
  left: 50%;
  z-index: -1;
  -webkit-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);
  color: var(--white-color);
  width: 500px;
  opacity: 0;
  transition: 0.5s ease-in;
}
.mossane-dropdown > div > ul > .mossane-link > a {
  color: var(--white-color);
}
.mossane-dropdown:hover .mossane-dropdown-hover {
  z-index: 100;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
  background-color: var(--primary-color);
  opacity: 1;
}
.mossane-dropdown > div > ul > .mossane-link > a:hover {
  color: var(--secondary-color);
}
#mossane-menu-mobile {
  display: none;
}

#mossane-btn-burger {
  position: fixed;
  display: none;
  z-index: 2000;
  z-index: 200;
  right: 15px;
  top: 30px;
  height: 5px;
  width: 35px;
  background-color: /* var(--primary-color) */ red;
  overflow: visible;
  cursor: pointer;
}
#mossane-btn-burger::after {
  position: absolute;
  background-color: /* var(--primary-color) */ red;
  z-index: 100;
  content: "";
  top: 200%;
  left: 0%;
  width: 100%;
  height: 100%;
}
#mossane-btn-burger::before {
  position: absolute;
  background-color: /* var(--primary-color) */ red;
  z-index: 100;
  content: "";
  top: -200%;
  left: 0%;
  width: 100%;
  height: 100%;
}

#mossane-menu-mobile {
  position: fixed;
  margin: 0;
  z-index: 100;
  top: 0px;
  width: 100vw;
  height: 100vh;
  transition: 0.5s ease-in-out;
  background-color: var(--primary-color);
  flex-direction: column;
  justify-content: center;
}
.mossane-menu-mobile-open {
  right: 1000px;
}
.mossane-menu-mobile-close {
  right: 0px;
}
.mossane-divider {
  width: 40%;
}

hgroup {
  padding: 0;
  text-transform: uppercase;
}
hgroup > h1 {
  font-size: 5em;
}
hgroup > h2 {
  font-size: 3em;
  color: var(--secondary-color);
}
hgroup,
hgroup + p {
  text-align: left;
  margin: 20px;
  margin-left: 10%;
}
hgroup + p {
  margin-top: 20px;
  letter-spacing: 0.1em;
  font-size: 1.3em;
}
.mossane-scrolldown {
  position: absolute;
  cursor: pointer;
  z-index: -1;
  left: 0px;
  bottom: 200px;
  margin: 20px;
  margin-left: 10%;
}

@media screen and (max-width: 992px) {
  #logo {
    width: 50px;
    position: fixed;
    left: 15px;
    top: 5px;
  }
  #mossane-menu-mobile {
    display: flex;
    z-index: 1000;
  }
  .mossane-navbar {
    display: none;
  }
  .mossane-link > a {
    color: var(--white-color);
  }
  .mossane-link-active > a {
    color: var(--secondary-color);
  }
  .mossane-dropdown-hover {
    position: relative;
    top: 0%;
    left: 0%;
    z-index: 10;
    -webkit-transform: translate(0%, 0%);
            transform: translate(0%, 0%);
    color: var(--white-color);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-max-content;
    height: max-content;
    opacity: 1;
  }
  .mossane-dropdown-hover > ul {
    flex-wrap: wrap;
  }
  .mossane-dropdown:hover .mossane-dropdown-hover {
    z-index: 10;
    -webkit-transform: translate(0%, 0%);
            transform: translate(0%, 0%);
    background-color: var(--primary-color);
    opacity: 1;
  }
  #mossane-btn-burger {
    display: flex;
    z-index: 5000;
  }

  hgroup > h1 {
    font-size: 3em;
  }
  hgroup > h2 {
    font-size: 1.8em;
  }
  hgroup + p {
    font-size: 1em;
  }
}

*{
    font-family: 'Rubik', 'Open Sans', sans-serif;
}
#main-app{
    min-height: 100vh;
    background-color: var(--grey-color);
}
#testimonial>h2{
    margin-bottom: 20px;
}

.mossane-illustration:nth-child(2){
    flex-direction: row-reverse;
}
.mossane-illustration-bloc1, .mossane-illustration-bloc2{
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    flex-wrap: nowrap;
    margin: 20px;
}
.mossane-illustration-bloc2>hgroup, .illustration-p{
    font-size: 1.3em;
    margin-left: 10px;
}
.illustration-p{
    font-weight: normal;
}
.mossane-illustration>*>.mossane-btn{
    position: absolute;

}
@media screen and (max-width: 992px) {
    .mossane-illustration-bloc1, .mossane-illustration-bloc2{
        background-size: cover;
        background-repeat: no-repeat;
        background-attachment: fixed;
        flex-wrap: nowrap;
        margin: 0px;
    }
}
@media screen and (max-width: 1200px) {
    .mossane-illustration-bloc2>hgroup, .illustration-p{
        font-size: 0.9em;
    }
}
#testimonial{
